import ProForm, {ProFormText} from "@ant-design/pro-form";
import React from 'react'
import logo from "../logo.svg";

export class RegisterPage extends React.Component{
    constructor() {
        super();
    }
    handleRegister(){
        //TODO: 注册验证
        this.props.history.push('/login-page');
    }
    render(){
        return(
            <div
                style={{
                    width: 330,
                    margin: 'auto',
                }}
            >
                <ProForm
                    onFinish={async () => {
                        // await waitTime(500);
                        this.handleRegister();
                    }}
                    submitter={{
                        searchConfig: {
                            submitText: '注册',
                        },
                        render: (_, dom) => dom.pop(),
                        submitButtonProps: {
                            size: 'large',
                            style: {
                                width: '100%',
                            },
                        },
                    }}
                >
                    <h1
                        style={{
                            textAlign: 'center',
                        }}
                    >
                        <img
                            style={{
                                height: '44px',
                                marginRight: 16,
                            }}
                            alt="logo"
                            src={logo}
                            // src="https://gw.alipayobjects.com/zos/rmsportal/KDpgvguMpGfqaHPjicRK.svg"
                        />
                        IoT Lord
                    </h1>
                    <div
                        style={{
                            marginTop: 12,
                            textAlign: 'center',
                            marginBottom: 40,
                        }}
                    >
                        IoT Lord是一个物联网管理系统，前端采用React，后端采用<del>php</del>,<del>java</del>,spring boot


                    </div>

                    <ProFormText
                        fieldProps={{
                            size: 'large',
                            // prefix: <MobileOutlined />,
                        }}
                        name="username"
                        label="账号"
                        placeholder="请输入账号"
                        // value={username}
                        // onChange={this.handleForm}
                        rules={[
                            {
                                min:6,
                                message: '账号长度至少6个字符!'
                            },
                            {
                                required: true,
                                message: '请输入账号!',
                            }
                            // ,
                            // {
                            //     pattern: /^1\d{10}$/,
                            //     message: '不合法的手机号格式!',
                            // },
                        ]}
                    />
                    <ProFormText.Password
                        fieldProps={{
                            size: 'large',
                            // prefix: <MobileOutlined />,
                        }}
                        width="md"
                        name="password"
                        label="密码"
                        placeholder="请输入密码"
                        // value={password}
                        // onChange={this.handleForm}
                        rules={[
                            {
                                min:6,
                                message: '密码至少6个字符!'
                            },
                            {
                                required: true,
                                message: '请输入密码!',
                            }
                        ]}
                    />
                    <ProFormText
                        fieldProps={{
                            size: 'large',
                        }}
                        name="email"
                        label="邮箱"
                        placeholder="请输入邮箱"

                        rules={[
                            {
                                required: true,
                                message: '请输入邮箱!',
                            }
                            ,
                            {
                                // pattern: /^1\d{10}\@$/,
                                pattern: /^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.]){1,2}[A-Za-z\d]{2,5}$/,
                                message: '不合法的邮箱格式!',
                            },
                        ]}
                    />

                </ProForm>
            </div>
        )
    }
}