import {Map} from "react-amap";
import React from "react";
// import Breadcrumb, from "antd";

import {Overview} from'./subpages/Overview'
import {Setting} from'./subpages/Setting'
import {Link, Route} from "react-router-dom";
import {MapView} from "../components/MapView";
import {Layout} from "antd";

import { Menu, Button } from 'antd';

// import {Tabbar} from "../components/Tabbar";
import {DesktopOutlined, PieChartOutlined, SettingOutlined} from "@ant-design/icons";
// import Sider from "antd";

const{Header,Content, Footer,  Sider} = Layout;
// const { SubMenu } = Menu;

export  class MainPage extends React.Component {
    state = {
        collapsed: false,
        // isValidated: false
    };
    constructor() {
        super();
        this.state={
            collapsed: false,
            // isValidated: true
        }
    }

    onCollapse = collapsed => {
        console.log(collapsed);
        this.setState({collapsed});
    };
    handleLogout=()=>{
        // this.setState({
        //     isValidated:false
        // })
        this.props.history.push('/');
    }
    render() {
        const {collapsed} = this.state;
        return (

            <Layout style={{minHeight: '100vh'}}>
                <Sider collapsible collapsed={collapsed} onCollapse={this.onCollapse}>
                    {/*<div className="logo" />*/}
                    <Menu theme="dark" defaultSelectedKeys={['1']} mode="inline">
                        <Menu.Item key="1" icon={<DesktopOutlined/>}>
                            总览
                            <Link to='/main-page'></Link>
                        </Menu.Item>
                        <Menu.Item key="2" icon={<PieChartOutlined/>}>
                            统计
                            <Link to='/main-page/overview'></Link>
                        </Menu.Item>
                        <Menu.Item key="3" icon={<SettingOutlined/>}>
                            设置
                            <Link to='/main-page/setting'></Link>
                        </Menu.Item>
                    </Menu>
                </Sider>
                <Layout className="site-layout">
                    <Header className="site-layout-background" style={{padding: 0}}/>
                    <Content style={{margin: '0 16px'}}>
                        <Button onClick={this.handleLogout} style={{position:'absolute',bottom:20,right:20}}>登出</Button>
                        <div className="site-layout-background" style={{padding: 24, minHeight: 360}}>
                            <Route exact path='/main-page' component={MapView}></Route>
                            <Route path='/main-page/overview' component={Overview}></Route>
                            <Route path='/main-page/setting' component={Setting}></Route>
                        </div>
                    </Content>
                    <Footer style={{textAlign: 'center'}}>Created by @wewewexiao2008</Footer>
                </Layout>
            </Layout>
        );


    }
    componentDidUpdate(prevProps: Readonly<P>, prevState: Readonly<S>, snapshot: SS) {

    }
}