const waitTime = (time: number = 100) => {
    return new Promise((resolve) => {
        setTimeout(() => {
            resolve(true);
        }, time);
    });
};



class Return extends React.Component{

    render() {
        return (
            <div>
                <Mouse>
                    {(mouse)=><div style={{
                        position:'absolute',
                        top:mouse.y-30,
                        left:mouse.x-80,
                        color:mouse.down?"red":"blue"
                    }
                    }>{mouse.x},{mouse.y}</div>}
                </Mouse>
                <div style={{position: 'fixed' ,top:'10px',right:'20px'}}>

                    <Link to={'/'}>主页</Link>
                </div>
            </div>

        );
    }

}
// let isLogin = false;
class App extends React.Component{
    state={
        user:'',
        validated:false
    }
constructor() {
    super();
    this.state={
        user:'',
        validated:false
    };
    // this.props.history.push('/login-page');
}

    render(){

        return (
                <Router>
                    <Route path = '/' component={Return} ></Route>
                    <Route path = '/register-page' component={RegisterPage}></Route>
                    <Route path = '/login-page' component={LoginPage} ></Route>
                    <Route exact path = '/' component={LoginPage} ></Route>
                    {/*<Route exact path = '/' component={Mouse} ></Route>*/}
                    <Route path = '/main-page' component={MainPage}></Route>
                </Router>

      );
    }
}


export default App;
