import ProForm, {ProFormText} from "@ant-design/pro-form";
import logo from "../logo.svg";
import {Link} from "react-router-dom";
import {Map} from "react-amap";
import React from 'react'
import {Mouse} from '../components/Mouse'

export class LoginPage extends React.Component{
    constructor() {
        super();
        this.state={

        }
    }
    handleLogin() {
        //TODO:登陆验证
        this.props.history.push('/main-page');
    }
    render(){
        return(

            <div className="App">

                <div
                    style={{
                        width: 330,
                        margin: 'auto',
                    }}
                >
                    <ProForm
                        onFinish={async () => {
                            // await waitTime(500);
                            this.handleLogin();
                        }}
                        submitter={{
                            searchConfig: {
                                submitText: '登录',
                            },
                            render: (_, dom) => dom.pop(),
                            submitButtonProps: {
                                size: 'large',
                                style: {
                                    width: '100%',
                                },
                            },
                        }}
                    >
                        <h1
                            style={{
                                textAlign: 'center',
                            }}
                        >
                            <img
                                style={{
                                    height: '44px',
                                    marginRight: 16,
                                }}
                                alt="logo"
                                src={logo}
                                // src="https://gw.alipayobjects.com/zos/rmsportal/KDpgvguMpGfqaHPjicRK.svg"
                            />
                            IoT Lord
                        </h1>
                        <div
                            style={{
                                marginTop: 12,
                                textAlign: 'center',
                                marginBottom: 40,
                            }}
                        >
                            IoT Lord是一个物联网管理系统，前端采用React，后端采用<del>php</del>,<del>java</del>,spring boot


                        </div>
                        <ProFormText
                            fieldProps={{
                                size: 'large',
                                // prefix: <MobileOutlined />,
                            }}
                            name="username"
                            label="账号"
                            placeholder="请输入账号"
                            // value={username}
                            // onChange={this.handleForm}
                            rules={[
                                {
                                    min:6,
                                    message: '账号长度至少6个字符!'
                                },
                                {
                                    required: true,
                                    message: '请输入账号!',
                                }
                                // ,
                                // {
                                //     pattern: /^1\d{10}$/,
                                //     message: '不合法的手机号格式!',
                                // },
                            ]}
                        />
                        <ProFormText.Password
                            fieldProps={{
                                size: 'large',
                                // prefix: <MobileOutlined />,
                            }}
                            width="md"
                            name="password"
                            label="密码"
                            placeholder="请输入密码"
                            // value={password}
                            // onChange={this.handleForm}
                            rules={[
                                {
                                    min:6,
                                    message: '密码至少6个字符!'
                                },
                                {
                                    required: true,
                                    message: '请输入密码!',
                                }
                            ]}
                        />

                    </ProForm>
                </div>
                <Mouse>
                    {(mouse)=><div style={{
                        position:'absolute',
                        top:mouse.y-30,
                        left:mouse.x-80,
                        color:mouse.down?"red":"blue"
                    }
                    }>{mouse.x},{mouse.y}</div>}
                </Mouse>

                <Link to={'/register-page'}>没有账号,去注册</Link>

                {/*<button onClick={this.handleGoRegister} type="primary">去注册</button>*/}

                <div id = 'clock' style={{position:'relative',bottom:'-50px'}}>

                </div>

                {/*<div style={{width: '100%', height: '400px'}} >*/}
                {/*    /!*normal（默认样式）、dark（深色样式）、light（浅色样式）、fresh(osm清新风格样式)、blue_night*!/*/}
                {/*    <Map mapStyle='dark' amapkey={'090e2ca73ca5c8964b96a2d06d87f3c2'} />*/}
                {/*</div>*/}

            </div>
        )
    }
}